<template>
  <div>
    <van-nav-bar title="登录" left-arrow @click-left="$router.push('/myjp')" />

    <van-form @submit="onSubmit">
      <van-field
        v-model="em"
        name="邮箱"
        label="邮箱"
        placeholder="邮箱"
        :rules="[{ required: true, message: '请填写邮箱号' }]"
      />

      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button
          ref="btn"
          @Submit="onSubmit"
          round
          block
          type="info"
          color="#ff464e"
          native-type="submit"
        >登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      em: "",
      password: "",
      bool: false
    };
  },
  methods: {
    onSubmit() {
      // console.log( this.username);
      this.$axios
        .post("http://127.0.0.1:3000/login", {
          emial: this.em,
          password: this.password
        })
        .then(res => {
          // console.log(res);
          if (res.data.err == 0) {
            Toast('登录成功')
            this.$store.state.balance = 200
            this.$store.state.loginBool = true;
            this.$store.state.username = this.em;
            setTimeout(() => {
              this.$router.push("/home");
            }, 1000);
          }else{
            Toast("登录失败")
          }
        });
    }
  },
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.bool = true;
  },
  watch: {}
};
</script>

<style lang="scss" scoped>
</style>